.current-weather-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.current-weather-background {
  background: rgba(188, 187, 215, 0.4);
  background: linear-gradient(
    0deg,
    rgba(179, 204, 161, 0.4) 25%,
    rgba(0, 181, 255, 0.1) 100%
  );

  border-radius: 5px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.container {
  border: 2px solid lightgray;
  display: inline-block;
  margin: 0 0.5em;
  padding: 0.5em;
  background: rgba(188, 187, 215, 0.4);
  background: linear-gradient(
    0deg,
    rgba(179, 204, 161, 0.4) 25%,
    rgba(0, 181, 255, 0.1) 100%
  );

  border-radius: 5px;
}

@media (max-width: 500px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  background-color: #fcfefe;
  font-family: "Montserrat Alternates", sans-serif;
  color: #444;
  max-width: 600px;
  margin: 0 auto;
}

header,
footer {
  text-align: center;
  margin: 1em 0;
}

h3 {
  margin: 1em 0.5em;
  border-bottom: 2px solid lightgray;
  text-align: center;
}

p {
  margin: 0.5em 0.5em;
  font-size: 0.9rem;
}

button {
  padding: 0.2em 0.6em;
  margin: 1em 1em;
  border-radius: 5px;
  border: 2px solid lightgray;
  background-color: aliceblue;
}

button:hover {
  border: 2px solid lightblue;
}

label {
  margin: 1em 1em;
}

input {
  padding: 0.3em;
  border-radius: 5px;
  border: 2px solid lightgray;
}

.first-message {
  margin: 2em;
  border: 1px solid lightgray;
  border-radius: 5px;
  padding: 1em;
  text-align: center;
}

